.header {
    background-color: #fff;
    border: 1px solid;
}

.header__inner {
    height: 60px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: center;
    align-items: center;
}

@media screen and (min-width: 768px) {
    .header__inner {
        height: 100px;
    }
}

.header__wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
}

.header__logo {
    margin-right: 45px;
}

@media screen and (min-width: 576px) {
    .header__logo {
        margin-right: 36px;
    }
}

@media screen and (min-width: 768px) {
    .header__logo {
        width: 130px;
    }
}

.header__phone-city {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #5286EC;
}

@media screen and (min-width: 576px) {
    .header__phone-city {
        display: none;
    }
}

.header__phone {
    display: none;
}

@media screen and (min-width: 576px) {
    .header__phone {
        display: block;
    }
}

.header__phone span {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #B3B3B3;
}

.header__phone__phone-link {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #37373F;
}

@media screen and (min-width: 1280px) {
    .header__user {
        display: none;
    }
}

.main {
    background-image: url(./../images/main-bg.jpg);
    background-size: cover;
    background-position: center;
    color: #fff;
}

.main__inner {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
    height: 655px;
}

@media screen and (min-width: 768px) {
    .main__inner {
        height: 840px;
    }
}

.main__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 39px;
    color: #fff;
    max-width: 247px;
    margin-top: -6px;
}

.main__title span {
    color: #FC9B09;
}

@media (min-width: 768px) {
    .main__title {
        max-width: 544px;
        font-size: 40px;
        line-height: 49px;
    }
}

@media (min-width: 1280px) {
    .main__title {
        max-width: 724px;
    }
}

.main__price-box {
    height: 93px;
    width: 208px;
    background-image: url(./../images/price-box.svg);
    background-repeat: no-repeat;
    margin: 36px 0 14px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    padding: 6px 15px;
    margin-left: -5px;
}

.main__wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
}

.main__priceLabel {
    font-weight: 400;
    font-size: 18px;
    line-height: inherit;
    color: inherit;
}

.main__price {
    font-weight: 800;
    font-size: 30px;
    line-height: inherit;
    color: inherit;
}

.main__percent {
    border-radius: 50%;
    -webkit-filter: drop-shadow(0px 4px 15px rgba(255, 255, 255, 0.25)) drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
    filter: drop-shadow(0px 4px 15px rgba(255, 255, 255, 0.25)) drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
    max-height: 47px;
    margin-top: 10px;
    -webkit-transform: translateX(4px);
    transform: translateX(4px);
}

.main__btn {
    width: 275px;
    height: 68px;
    border: 3px solid #fff;
    border-radius: 7px;
    background-color: transparent;
    font-weight: 900;
    font-size: 16px;
    line-height: 20px;
    color: white;
    -webkit-filter: drop-shadow(0px 4px 70px rgba(255, 255, 255, 0.7)) drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
    filter: drop-shadow(0px 4px 70px rgba(255, 255, 255, 0.7)) drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
}

.info__inner {
    display: grid;
    grid-template-columns: 1fr;
}

.info__title {
    font-weight: 400;
    font-size: 28px;
    line-height: 34px;
    color: #37373F;
    text-align: center;
    padding: 40px 0 13px;
}

.info__img {
    border-radius: 4px;
    width: 100%;
}

.info__wrapper .info__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #37373F;
    max-width: 290px;
    margin: 30px 0 30px;
}

.info__wrapper .info__item {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #37373F;
    margin-bottom: 20px;
    margin-left: 35px;
}

.info__wrapper .info__item-time {
    position: relative;
}

.info__wrapper .info__item-time::before {
    content: "";
    background-image: url(../images/info/time.svg);
    display: block;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    left: -40px;
    bottom: -5px;
    width: 32px;
    height: 32px;
}

.info__wrapper .info__item-photo {
    position: relative;
}

.info__wrapper .info__item-photo::before {
    content: "";
    background-image: url(../images/info/photo.svg);
    display: block;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    left: -40px;
    bottom: -3px;
    width: 35px;
    height: 28px;
}

.info__wrapper .info__item-location {
    position: relative;
}

.info__wrapper .info__item-location::before {
    content: "";
    background-image: url(../images/info/location.svg);
    display: block;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    left: -40px;
    bottom: -7px;
    width: 33px;
    height: 33px;
}

.info__wrapper .info__item-speech {
    position: relative;
}

.info__wrapper .info__item-speech::before {
    content: "";
    background-image: url(../images/info/speech-bubble.svg);
    display: block;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    left: -40px;
    top: 2px;
    width: 31px;
    height: 31px;
}

.info__wrapper .info__item:last-child {
    margin-bottom: 50px;
}

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    font-family: 'Montserrat', sans-serif;
    min-width: 320px;
}
